import React from 'react';
import { Redirect, Route, Switch } from 'react-router';

import AboutPage from './AboutPage';
import CollectionsApp from './CollectionsApp';
import EvidenceApp from './EvidenceApp';
import ResourcesPage from './OtherResourcesPage';
import SuspectsApp, { SuspectsTableApp } from './SuspectsApp';

export const PATHS = {
  evidence: '/e',
  collections: '/c',
  suspects: '/gallery',
  suspectsTable: '/poi_table',
  about: '/about',
  resources: '/resources',
};

function Routes() {
  return (
    <Switch>
      <Route exact path="/">
        <Redirect to={PATHS.evidence} />
      </Route>
      <Route path={PATHS.about} component={AboutPage} />
      <Route path={PATHS.resources} component={ResourcesPage} />
      <Route path={PATHS.suspects} component={SuspectsApp} />
      <Route path={PATHS.suspectsTable} component={SuspectsTableApp} />
      <Route path={PATHS.evidence} exact component={EvidenceApp} />
      <Route path={`${PATHS.evidence}/:detailId`} component={EvidenceApp} />
      <Route exact path={PATHS.collections} component={CollectionsApp} />
      <Route
        path={`${PATHS.collections}/:collectionId?name=:name&collectionItems=:collectionItems&detail=:detailId`}
        component={CollectionsApp}
      />
    </Switch>
  );
}

export default Routes;
