import React from 'react';
import Modal from 'react-modal';
import { useParams, useHistory, useRouteMatch } from 'react-router-dom';

import { useLocation } from 'react-use';

import { Evidence } from '@jan6evidence/types';

import EvidenceDetail from '../EvidenceDetail';
import PageHeaderAndNav from '../PageHeaderAndNav';
import { HighlightContext } from '../shared/contexts';
import { useQuery } from '../shared/hooks';
import { debounce } from '../shared/util';
import {
  EvidenceResultsWrapper,
  EvidenceTableWrapper,
  EvidenceCount,
} from '../table-styled-components';

import EvidenceMapbox from './EvidenceMapbox';
import EvidenceTable from './EvidenceTable';
import EvidenceTimeline from './EvidenceTimeline';

Modal.setAppElement('#root');

export { EvidenceTable, EvidenceTimeline, EvidenceMapbox };

type StartEndTimes = [Date, Date] | null;

type Props = {
  evidence: Evidence[];
  timedEvidence: Evidence[];
  startEndTimes?: StartEndTimes;
  setStartEndTimes?: (se: StartEndTimes) => void;
  title: string;
  hideExplorerLink?: boolean;
  rootPath: string;
  getDetailPath: (id: string) => string;
};

const EvidenceViewer = ({
  evidence,
  timedEvidence,
  startEndTimes,
  setStartEndTimes,
  title,
  hideExplorerLink,
  rootPath,
  getDetailPath,
}: Props) => {
  const [highlight, setHighlight] = React.useState<string>('');
  let { detailId } = useParams<{ detailId?: string }>();
  const history = useHistory();

  // check query for detail ID if it's not in the pathname
  const query = useQuery();
  if (!detailId) {
    detailId = query.get('detail') || '';
  }

  const closeModal = React.useCallback(() => {
    history.push(rootPath);
  }, [history]);

  const debouncedHighlight = React.useCallback(debounce(setHighlight, 100), []);

  let prevEvidence;
  let nextEvidence;
  const detailEvidence = detailId
    ? evidence.find((r, i) => {
        if (r._id === detailId) {
          prevEvidence = evidence[i - 1];
          nextEvidence = evidence[i + 1];
          return true;
        }
        return false;
      })!
    : { _id: '' };

  return (
    <EvidenceResultsWrapper>
      <PageHeaderAndNav title={title} hideExplorerLink={hideExplorerLink} />
      <HighlightContext.Provider value={{ highlight, setHighlight: debouncedHighlight }}>
        <EvidenceMapbox evidence={evidence} onHover={debouncedHighlight} highlight={highlight} />
        <EvidenceTimeline
          startEndTimes={startEndTimes}
          onChangeTimes={setStartEndTimes}
          // We re-filter here, ignoring time time filter, so that we still show
          // evidence on the timeline that isn't in the selected time range
          evidence={timedEvidence}
        />
        <EvidenceCount>
          {evidence.length} bit{evidence.length === 1 ? '' : 's'} of evidence
        </EvidenceCount>
        <EvidenceTableWrapper>
          <EvidenceTable
            results={evidence}
            highlight={highlight}
            onHover={debouncedHighlight}
            getDetailPath={getDetailPath}
          />
        </EvidenceTableWrapper>
      </HighlightContext.Provider>
      <EvidenceDetail
        detailId={detailId}
        evidence={detailEvidence}
        onClose={closeModal}
        prev={prevEvidence}
        next={nextEvidence}
      />
    </EvidenceResultsWrapper>
  );
};

export default EvidenceViewer;
