import React from 'react';

import styled from 'styled-components';

import { Suspect } from '@jan6evidence/types';

const SuspectCardDiv = styled.div`
  padding: 0.25rem;
  flex: auto;
  min-width: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 280px;
  margin: 10px 5px;
  vertical-align: top;
`;

const SuspectThumbnailImg = styled.img`
  width: auto;
  height: 200px;
  vertical-align: middle;
  border: solid 1px #eee;
`;

const OverviewContainerDiv = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
`;

export const SuspectThumbnail = ({ primaryHashtag }: { primaryHashtag: string | undefined }) => {
  if (!primaryHashtag) {
    return null;
  }
  const thumbnailSrcUrl = `https://www.tommycarstensen.com/terrorism/suspects/images/${primaryHashtag.toLowerCase()}/thumbnail.png`;
  return <SuspectThumbnailImg src={thumbnailSrcUrl} alt={`${primaryHashtag} thumbnail image`} />;
};

// For now, point to TommyC's site. Replace with our own detail page once we have it!
const detailUrl = (suspect: Suspect): string =>
  `https://www.tommycarstensen.com/terrorism/suspects/individuals/${suspect.primaryHashtag}.html`;

const twitterSearchUrl = (suspect: Suspect): string =>
  `https://twitter.com/search?q=${suspect.primaryHashtag}`;

const getFbiLink = (suspect: Suspect) => {
  if (!suspect.fbiIdentifier) {
    return null;
  }
  const fbiNumberMatch = suspect.fbiIdentifier.match(/\d+$/);
  if (!fbiNumberMatch) {
    return null;
  }
  // Hack!! This is just a guess about the fbi URL. It's not always correct.
  const fbiUrl = `https://www.fbi.gov/wanted/capitol-violence-images/capitol-${fbiNumberMatch[1]}.png/view`;
  return <a href={fbiUrl}>FBI: {suspect.fbiIdentifier}</a>;
};

const SuspectCard = ({ suspect }: { suspect: Suspect }) => {
  if (!suspect.primaryHashtag) {
    return null;
  }
  const fbiLink = getFbiLink(suspect);
  return (
    <SuspectCardDiv data-title={`suspect-${suspect._id}`}>
      <a href={detailUrl(suspect)}>
        <SuspectThumbnail primaryHashtag={suspect.primaryHashtag} />
      </a>
      <a href={twitterSearchUrl(suspect)}>#{suspect.primaryHashtag}</a>
      {fbiLink || <span>&nbsp;</span>}
    </SuspectCardDiv>
  );
};

const SuspectsGallery = ({ suspects }: { suspects: Array<Suspect> }) => {
  return (
    <OverviewContainerDiv>
      {suspects.map((suspect) => (
        <SuspectCard key={suspect._id} suspect={suspect} />
      ))}
    </OverviewContainerDiv>
  );
};

export default SuspectsGallery;
